import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Usuario } from 'src/app/models/usuario';
import { UsuarioFormComponent } from './usuario-form/usuario-form.component';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { UsuarioService } from 'src/app/services/usuario.service';
import { AlertHelper } from 'src/app/helpers/alert.helper';

@Component({
  selector: 'empresa-usuarios',
  templateUrl: './empresa-usuarios.component.html',
  styleUrls: ['./empresa-usuarios.component.scss']
})
export class EmpresaUsuariosComponent implements OnInit, OnDestroy {
  public loading: boolean = false;
  public bsModalRef?: BsModalRef;
  private unsubscribe$ = new Subject<void>();

  @Input() empresaId: number;
  @Input() usuarios?: Usuario[];

  constructor(
    private modalService: BsModalService,
    private usuarioService: UsuarioService,
    private alertHelper: AlertHelper
  ) { }

  public ngOnInit(): void {
  }

  public ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  public openUsuarioForm(usuario?: Usuario): void {
    this.bsModalRef = this.modalService.show(UsuarioFormComponent, {
      initialState: {
        empresaId: this.empresaId,
        usuario: usuario,
      }
    });

    const component: UsuarioFormComponent = this.bsModalRef.content;

    component.onClose
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (usuario) => {
          const index = this.usuarios.findIndex(u => u.id == usuario.id);
          if (index === -1) {
            this.usuarios.unshift(usuario);
          } else {
            this.usuarios[index] = usuario;
          }
        }
      });
  }

  public destroy(usuario: Usuario): void {
    this.alertHelper.show(
      'warning',
      `Tem certeza que deseja remover o usuário ${usuario.nome}?`,
      { showCancelButton: true }
    )
    .then(confirmed => {
      if (confirmed) {
        this.loading = true;
        this.usuarioService.delete(usuario)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe({
            next: (usuario) => {
              this.loading = false;
              this.usuarios = this.usuarios.filter(u => u.id != usuario.id);
            },
            error: () => {
              this.loading = false;
            }
          })
      }
    })
  }
}
