import { Component, Input, OnChanges, OnDestroy, OnInit, SimpleChanges } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Observable, Subject } from 'rxjs';
import { map, takeUntil } from 'rxjs/operators';
import { Empresa } from 'src/app/models/empresa';
import { Usuario, UsuarioTipo } from 'src/app/models/usuario';
import { AuthService } from 'src/app/services/auth.service';
import { EmpresaService } from 'src/app/services/empresa.service';
import { AlertHelper } from 'src/app/helpers/alert.helper';
import { cnpjValidator } from 'src/app/helpers/validators.helper';

@Component({
  selector: 'empresa-form',
  templateUrl: './empresa-form.component.html',
  styleUrls: ['./empresa-form.component.scss']
})
export class EmpresaFormComponent implements OnInit, OnChanges, OnDestroy {
  public empresaPai = new FormControl('', Validators.required);
  public nomeEmpresa = new FormControl('', Validators.required);
  public nomeFantasia = new FormControl('', Validators.required);
  public cnpj = new FormControl('', [Validators.required, cnpjValidator()]);
  public fone = new FormControl('', Validators.required);
  public endereco = new FormControl('', Validators.required);
  public email = new FormControl('', [Validators.required, Validators.email]);
  public site = new FormControl('', Validators.required);
  public logo = new FormControl('', Validators.required);
  public isInvalid: boolean = false;
  public imageUrl?: string;
  public empresas$: Observable<Empresa[]>;
  public auth: Usuario;
  public loading: boolean = false;
  public loadingLogo: boolean = false;
  private unsubscribe$ = new Subject<void>();

  @Input() empresa?: Empresa;

  constructor(
    private alertHelper: AlertHelper,
    private authService: AuthService,
    private empresaService: EmpresaService,
    private router: Router
  ) {}

  public ngOnInit(): void {
    this.auth = this.authService.getUser();
    this.empresas$ = this.empresaService.list()
      .pipe(
        map(c => c.filter(e => e.id !== this.empresa?.id ))
      );
  }

  public ngOnChanges(changes: SimpleChanges): void {
    if (this.empresa) {
      this.nomeEmpresa.setValue(this.empresa.nome_empresa);
      this.nomeFantasia.setValue(this.empresa.nome_fantasia);
      this.cnpj.setValue(this.empresa.cnpj);
      this.fone.setValue(this.empresa.fone);
      this.endereco.setValue(this.empresa.endereco);
      this.email.setValue(this.empresa.email);
      this.site.setValue(this.empresa.site);

      this.imageUrl = this.empresa.logo.toString();

      if (this.auth.tipo == UsuarioTipo.admin) {
        this.empresaPai.setValue(this.empresa.empresa_pai);
      }

      this.logo.clearValidators();
      this.logo.updateValueAndValidity();
    }
  }

  public ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  public logoChanged(event: Event): void {
    const file = (event.target as HTMLInputElement).files[0];
    if (file && file.type.startsWith('image/')) {
      const reader = new FileReader();
      reader.onload = () => {
        if (this.empresa?.id) {
          this.updateLogo(file);
        } else {
          this.imageUrl = reader.result.toString();
          this.logo.setValue(file);
        }
      };
      reader.onerror = () => {
        this.alertHelper.toast('error', 'Imagem não pode ser carregada. Tente novamente.');
      };
      reader.readAsDataURL(file);
    } else {
      this.alertHelper.toast('error', 'A logo da empresa precisa ser uma imagem.');
    }
  }

  public submit(): void {
    this.isInvalid = this.nomeEmpresa.errors?.required
      || this.nomeFantasia.errors?.required
      || this.cnpj.errors?.required
      || this.cnpj.errors?.cnpj
      || this.fone.errors?.required
      || this.endereco.errors?.required
      || this.email.errors?.required
      || this.email.errors?.email
      || this.site.errors?.required
      || this.logo.errors?.required;

    if (this.auth.tipo == UsuarioTipo.admin) {
      this.isInvalid = this.isInvalid || this.empresaPai.errors?.required;
    }

    if (!this.isInvalid) {
      this.empresa = {
        ...this.empresa,
        nome_empresa: this.nomeEmpresa.value,
        nome_fantasia: this.nomeFantasia.value,
        cnpj: this.cnpj.value,
        fone: this.fone.value,
        endereco: this.endereco.value,
        email: this.email.value,
        site: this.site.value,
        logo: this.logo.value,
      };

      if (this.auth.tipo == UsuarioTipo.admin) {
        this.empresa.empresa_pai = this.empresaPai.value;
      }

      if (this.empresa?.id) {
        this.empresa.logo = undefined;
        this.updateEmpresa();
      } else {
        this.createEmpresa();
      }
    }
  }

  private createEmpresa(): void {
    this.loading = true;
    this.empresaService.create(this.empresa)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: () => {
          this.loading = false;
          this.alertHelper.toast('success', 'Empresa cadastrada com sucesso.');
          this.router.navigateByUrl('/empresas');
        },
        error: () => {
          this.loading = false;
        }
      });
  }

  private updateEmpresa(): void {
    this.loading = true;
    this.empresaService.update(this.empresa)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: () => {
          this.loading = false;
          this.alertHelper.toast('success', 'Empresa atualizada com sucesso.');
        },
        error: () => {
          this.loading = false;
        }
      });
  }

  private updateLogo(logo: File): void {
    this.loadingLogo = true;
    this.empresaService.updateLogo(this.empresa.id, logo)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (empresa) => {
          this.loadingLogo = false;
          this.imageUrl = empresa.logo.toString();
          this.alertHelper.toast('success', 'Logo alterada com sucesso.');
        },
        error: () => {
          this.loadingLogo = false;
        }
      });
  }
}
