import { HttpErrorResponse } from '@angular/common/http';
import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { USER_TYPES, UserType } from 'src/app/data/user-types.data';
import { AlertHelper } from 'src/app/helpers/alert.helper';
import { ResponseError } from 'src/app/models/response-error';
import { Usuario } from 'src/app/models/usuario';
import { UsuarioService } from 'src/app/services/usuario.service';

@Component({
  selector: 'usuario-form',
  templateUrl: './usuario-form.component.html',
  styleUrls: ['./usuario-form.component.scss']
})
export class UsuarioFormComponent implements OnInit, OnDestroy {
  public nome = new FormControl('', [Validators.required]);
  public email = new FormControl('', [Validators.required, Validators.email]);
  public nasc = new FormControl('', [Validators.required]);
  public tipo = new FormControl('', [Validators.required]);
  public genero = new FormControl('', [Validators.required]);
  public password = new FormControl('', [Validators.required]);
  public isInvalid: boolean = false;
  public loading: boolean = false;
  public userTypes: UserType[] = [];
  public onClose = new Subject<Usuario>();
  private unsubscribe$ = new Subject<void>();

  @Input() empresaId: number;
  @Input() usuario?: Usuario;

  constructor(
    public bsModalRef: BsModalRef,
    private usuarioService: UsuarioService,
    private alertHelper: AlertHelper
  ) { }

  public ngOnInit(): void {
    this.userTypes = USER_TYPES.filter(u => u.id != 1);

    if (this.usuario) {
      this.nome.setValue(this.usuario.nome);
      this.email.setValue(this.usuario.email);
      this.nasc.setValue(this.usuario.nasc);
      this.tipo.setValue(this.usuario.tipo);
      this.genero.setValue(this.usuario.genero);

      this.password.clearValidators();
      this.password.updateValueAndValidity();
    }
  }

  public ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  public submit(): void {
    this.isInvalid = this.nome.invalid
      || this.email.invalid
      || this.nasc.invalid
      || this.tipo.invalid
      || this.genero.invalid
      || this.password.invalid;

    if (!this.isInvalid) {
      this.usuario = {
        ...this.usuario,
        empresa_id: this.empresaId,
        nome: this.nome.value,
        email: this.email.value,
        nasc: this.nasc.value,
        tipo: this.tipo.value,
        genero: this.genero.value,
      };

      if (this.password.value) {
        this.usuario.password = this.password.value;
      }

      if (this.usuario?.id) {
        this.updateUsuario();
      } else {
        this.createUsuario();
      }
    }
  }

  private createUsuario(): void {
    this.loading = true;
    this.usuarioService.create(this.usuario)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (usuario) => {
          this.loading = false;
          this.alertHelper.toast('success', 'Usuário cadastrado com sucesso.');
          this.onClose.next(usuario);
          this.bsModalRef.hide();
        },
        error: (e) => {
          this.loading = false;
          if (e instanceof ResponseError) {
            this.alertHelper.show('error', e.getMessage(), {
              confirmButtonText: 'Entendi'
            });
          }
        }
      });
  }

  private updateUsuario(): void {
    this.loading = true;
    this.usuarioService.update(this.usuario)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (usuario) => {
          this.loading = false;
          this.alertHelper.toast('success', 'Usuário atualizado com sucesso.');
          this.onClose.next(usuario);
          this.bsModalRef.hide();
        },
        error: () => {
          this.loading = false;
        }
      });
  }
}
