import { Empresa } from './empresa';

export interface Usuario {
  id: number;
  empresa_id: number;
  nome: string;
  email: string;
  genero: string;
  nasc: string;
  tipo: UsuarioTipo;
  password?: string;
  created_at?: string;
  updated_at?: string;
  empresa: Empresa;
}

export enum UsuarioTipo {
  admin = 1,
  director = 2,
  coordinator = 3,
  employee = 4,
}
